.sidebar-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 998;
    display: none;
}

.control-sidebar {
    position: fixed;
    top: 0;
    left: 0;
    width: 280px;
    height: 100vh;
    background: var(--surface-color);
    border-right: 1px solid var(--border-color);
    transform: translateX(-100%);
    transition: transform 0.3s ease;
    z-index: 999;
    display: flex;
    flex-direction: column;
}

.control-sidebar.sidebar-open {
    transform: translateX(0);
}

.mobile-menu-header {
    padding: 1rem;
    border-bottom: 1px solid var(--border-color);
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: var(--background-color);
}

.close-btn {
    background: none;
    border: none;
    color: var(--text-primary);
    font-size: 1.5rem;
    cursor: pointer;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    transition: background-color 0.2s ease;
}

.close-btn:hover {
    background: var(--background-color);
}

.sidebar-nav {
    flex: 1;
    padding: 1rem 0;
    display: flex;
    flex-direction: column;
}

.nav-item {
    background: none;
    border: none;
    color: var(--text-primary);
    padding: 1rem 1.5rem;
    text-align: left;
    cursor: pointer;
    transition: all 0.3s;
    border-left: 4px solid transparent;
    font-size: 0.95rem;
}

.nav-item:hover {
    background: var(--background-color);
    border-left-color: var(--primary-color);
}

.nav-item.active {
    background: var(--background-color);
    border-left-color: var(--primary-color);
    font-weight: 600;
}

@media (min-width: 769px) {
    .sidebar-overlay {
        display: none !important;
    }

    .control-sidebar {
        position: sticky;
        top: 70px;
        transform: translateX(0) !important;
        height: calc(100vh - 70px);
    }

    .mobile-menu-header {
        display: none;
    }
}

@media (max-width: 768px) {
    .sidebar-overlay {
        display: block;
    }

    .mobile-menu-header {
        display: flex;
    }

    .control-sidebar {
        width: 85%;
        max-width: 300px;
    }
}