.inspector-header {
    background: var(--bg-surface);
    border-bottom: 1px solid var(--border-color);
    box-shadow: var(--shadow);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1100;
    height: 70px;
    display: flex;
    align-items: center;
    transition: background-color 0.3s ease, border-color 0.3s ease;
}

.inspector-header-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: var(--space-md) var(--space-lg);
    width: 100%;
}

.inspector-header-left {
    display: flex;
    align-items: center;
    gap: var(--space-md);
}

.inspector-mobile-menu-btn {
    display: none;
    flex-direction: column;
    background: none;
    border: 1px solid var(--border-color);
    padding: var(--space-sm);
    border-radius: 8px;
    cursor: pointer;
    color: var(--text-secondary);
    width: 40px;
    height: 40px;
    justify-content: center;
    align-items: center;
    transition: all 0.2s ease;
}

.inspector-mobile-menu-btn:hover {
    background-color: var(--bg-secondary);
    color: var(--text-primary);
}

.inspector-mobile-menu-btn span {
    display: block;
    height: 2px;
    width: 20px;
    background: currentColor;
    margin: 2px 0;
    border-radius: 1px;
    transition: all 0.3s ease;
}

.inspector-logo-text {
    font-size: 1.25rem;
    font-weight: 600;
    color: var(--text-primary);
}

.inspector-header-right {
    display: flex;
    align-items: center;
    gap: var(--space-md);
}

.inspector-theme-toggle-btn {
    background: none;
    border: 1px solid var(--border-color);
    padding: var(--space-sm);
    border-radius: 8px;
    cursor: pointer;
    color: var(--text-secondary);
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s ease;
    font-size: 1.2rem;
}

.inspector-theme-toggle-btn:hover {
    background-color: var(--bg-secondary);
    color: var(--text-primary);
    transform: scale(1.05);
}

.inspector-user-info {
    display: flex;
    align-items: center;
    gap: var(--space-sm);
}

.inspector-user-avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: var(--primary);
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-weight: 600;
    border: 2px solid var(--border-color);
}

.inspector-user-details {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.inspector-user-name {
    font-weight: 600;
    font-size: 0.95rem;
    color: var(--text-primary);
}

.inspector-user-role {
    font-size: 0.85rem;
    color: var(--text-secondary);
}

.inspector-logout-btn {
    background: none;
    border: 1px solid var(--border-color);
    padding: var(--space-sm) var(--space-md);
    border-radius: 8px;
    cursor: pointer;
    color: var(--text-secondary);
    font-weight: 500;
    transition: all 0.2s ease;
}

.inspector-logout-btn:hover {
    background-color: var(--bg-secondary);
    color: var(--text-primary);
}

/* Мобильные стили */
@media (max-width: 768px) {
    .inspector-header {
        height: 60px;
    }

    .inspector-header-content {
        padding: var(--space-sm) var(--space-md);
    }

    .inspector-mobile-menu-btn {
        display: flex;
    }

    .inspector-user-details {
        display: none;
    }

    .inspector-logo-text {
        font-size: 1.125rem;
    }
}

@media (max-width: 480px) {
    .inspector-header-content {
        padding: var(--space-xs) var(--space-sm);
    }

    .inspector-theme-toggle-btn,
    .inspector-logout-btn {
        padding: var(--space-xs);
        width: 36px;
        height: 36px;
    }

    .inspector-mobile-menu-btn {
        width: 36px;
        height: 36px;
        padding: var(--space-xs);
    }

    .inspector-user-avatar {
        width: 32px;
        height: 32px;
        font-size: 0.9rem;
    }

    .inspector-logo-text {
        font-size: 1rem;
    }
}