.inspector-content-section {
    background: var(--inspector-surface-color);
    border: 1px solid var(--inspector-border-color);
    border-radius: 12px;
    padding: var(--space-xl);
}

.inspector-content-section h2 {
    font-size: 1.5rem;
    font-weight: 600;
    color: var(--inspector-text-primary);
    margin-bottom: var(--space-sm);
}

.inspector-content-section p {
    color: var(--inspector-text-secondary);
    margin-bottom: var(--space-lg);
}

.inspector-content-placeholder {
    background: var(--inspector-background-color);
    border: 2px dashed var(--inspector-border-color);
    border-radius: 8px;
    padding: var(--space-xl);
    text-align: center;
    color: var(--inspector-text-tertiary);
}