.inspector-sidebar {
    position: fixed;
    top: 0;
    left: 0;
    width: 280px;
    height: 100vh;
    background: var(--bg-surface);
    border-right: 1px solid var(--border-color);
    transform: translateX(-100%);
    transition: transform 0.3s ease, background-color 0.3s ease, border-color 0.3s ease;
    z-index: 999;
    display: flex;
    flex-direction: column;
}

.inspector-sidebar.inspector-sidebar-open {
    transform: translateX(0);
}

.inspector-mobile-menu-header {
    padding: 1rem;
    border-bottom: 1px solid var(--border-color);
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: var(--bg-primary);
    transition: background-color 0.3s ease, border-color 0.3s ease;
}

.inspector-close-btn {
    background: none;
    border: none;
    color: var(--text-primary);
    font-size: 1.5rem;
    cursor: pointer;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    transition: background-color 0.2s ease;
}

.inspector-close-btn:hover {
    background: var(--bg-secondary);
}

.inspector-sidebar-nav {
    flex: 1;
    padding: 1rem 0;
    display: flex;
    flex-direction: column;
}

.inspector-nav-item {
    background: none;
    border: none;
    color: var(--text-primary);
    padding: 1rem 1.5rem;
    text-align: left;
    cursor: pointer;
    transition: all 0.3s;
    border-left: 4px solid transparent;
    font-size: 0.95rem;
}

.inspector-nav-item:hover {
    background: var(--bg-secondary);
    border-left-color: var(--primary);
}

.inspector-nav-item.inspector-nav-item-active {
    background: var(--bg-secondary);
    border-left-color: var(--primary);
    font-weight: 600;
}

/* Десктопная версия */
@media (min-width: 769px) {
    .inspector-sidebar {
        position: sticky;
        top: 70px;
        transform: translateX(0) !important;
        height: calc(100vh - 70px);
    }

    .inspector-mobile-menu-header {
        display: none;
    }
}

/* Мобильная версия */
@media (max-width: 768px) {
    .inspector-mobile-menu-header {
        display: flex;
    }

    .inspector-sidebar {
        width: 85%;
        max-width: 300px;
    }
}

@media (max-width: 480px) {
    .inspector-sidebar {
        width: 90%;
    }

    .inspector-nav-item {
        padding: 0.9rem 1.25rem;
    }
}